@import url('https://fonts.googleapis.com/css2?family=Roboto:wght@300&display=swap');

.App {
  text-align: center;
  font-family: 'Roboto', sans-serif;
  height: 100vh;
}

#loader{
  text-align: center;
  margin: 300px;
}


.loader {
  position: absolute;
  margin-top: 130px;
  width: 350px;
  height: 350px;
  border-radius: 50%;
  background: linear-gradient(rgb(121, 251, 255),rgb(255, 96, 228));
  animation: animate 1s linear infinite;
}

@keyframes animate {
  0% {
      transform: rotate(0deg);
  }
  100% {
      transform: rotate(360deg);
  }
}

.loader span {
  position: absolute;
  width: 150px;
  height: 150px;
  border-radius: 50%;
  background: linear-gradient(rgb(121, 251, 255),rgb(255, 96, 228));
}

.loader span:nth-child(1) {
  filter: blur(5px);
}

.loader span:nth-child(2) {
  filter: blur(10px);
}

.loader span:nth-child(3) {
  filter: blur(25px);
}

.loader span:nth-child(4) {
  filter: blur(50px);
}

.loader:after {
  content: '';
  position: absolute;
  top: 10px;
  left: 10px;
  right: 10px;
  bottom: 10px;
  background: #f1f1f1;
  border: solid white 10px;
  border-radius: 50%;
}

/*Social icons*/

.slider{
  position: fixed;
  margin-top: 60px;
  height: 70vh;
  float: left;
  flex-direction: column;
  justify-content: center;
  transform: translate(-250px, 0);
}

.slider a{
  color:white;
  background: linear-gradient(45deg, rgba(121, 251, 255, 0.788),rgba(255, 96, 228, 0.788));
  font-size: 25px;
  font-weight: 500;
  text-decoration: none;
  display: block;
  margin: 5px;
  padding: 20px;
  width: 300px;
  text-align: right;
  border-radius: 50px;
  transition: .7s;
}

div .slider a:hover{
  transform: translateX(140px);
}

.social i{
  margin-left: 10px;
  font-size: 30px;
  width: 30px;
  height: 30px;
}

.fa-facebook{
  transition: 0.3s ease-in-out;
}
.fa-youtube{
  transition: 0.3s ease-in-out;
}

.fa-github{
  transition: 0.3s ease-in-out;
}

.fa-linkedin{
  transition: 0.3s ease-in-out;
}

.fa-twitter{
  transition: 0.3s ease-in-out;
}

.fa-codepen{
  transition: 0.3s ease-in-out;
}

.fa-facebook:hover{
  color: blue;
}
.fa-youtube:hover{
  color: red;
}
.fa-twitter:hover{
  color: rgb(0, 155, 245);
}
.fa-github:hover{
  color: rgb(17, 17, 27);
}
.fa-codepen:hover{
  color: rgb(17, 17, 27);
}
.fa-linkedin:hover{
  color: rgb(92, 108, 253);
}

/*project section*/
.bg-project{
  background: linear-gradient(270deg,rgba(121, 251, 255, 0.767),rgba(255, 96, 228, 0.712));
  text-align: center;
  padding: 100px 10px 50px 50px;
  border-radius: 50px 500px 100px 50px;
  margin-top: -45px;
}

/*contact section*/

.bg-contact{
  background: linear-gradient(120deg,rgba(121, 251, 255, 0.767),rgba(255, 96, 228, 0.712));
  text-align: center;
  padding: 100px 10px 50px 50px;
  border-radius: 500px 50px 100px 50px;
  margin-top: -45px;
}


/*footer*/
.footer{
  padding: 50px 0 10px 0;
}