#sergio{
    max-height: 250px;
    max-width: 250px;
    border: 100%;
    border-color: rgb(91, 132, 245);
}

body{
    color: white;
}

#html{
    width: 90%;
}

#css{
    width: 80%;
}
#js{
    width: 65%;
}
#angular{
    width: 60%;
}
#react{
    width: 60%;
}

#ionic{
    width: 60%;
}


/*skills bar*/
.bg-pbar{
    background: linear-gradient(270deg, rgb(91, 132, 245), rgb(199, 3, 199));
}

strong{
    text-align: left;
    display: flex
}

.photobio{
    position: absolute;
    width: 100%;
    opacity: 0;
    transition: .1s ease;
    background-color: radial-gradient(270deg, rgb(121, 251, 255, 0.4),rgba(255, 96, 228, 0.473));
}

.bio{
    display: absolute;    
    transition: .1s;
    filter: grayscale(0);
}

.bio:hover{
    filter: grayscale(1);
    

}

/*background jumbotron*/
.bg-sacv{
    background: linear-gradient(270deg, rgb(121, 251, 255, 0.4),rgba(255, 96, 228, 0.473)), url(../assets/bg-header.jpg);
    -webkit-background-size: cover;
     -moz-background-size: cover;
     background-size: cover;
     -o-background-size: cover;
     border-radius: 0.3rem;
     animation: Gradient 15s ease infinite;
}

@-webkit-keyframes Gradient{
    0%{
        background-position: 0% 50%;
    }
    50%{
        background-position: 100% 50%;
    }
    100%{
        background-position: 0% 50%;
    }
}


.btn-purple{
    background-color: rgba(255, 96, 228, 0.473);
    color: seashell;
}

.btn-purple:hover{
    background-color: rgb(255, 96, 228);
}

#sacv {
	transition: 0.1s;
}

#sacv:hover{
    color:  rgba(65, 141, 255, 0.979);
    
}

#logo{
    height: 30px;
    width: auto;
}

.bg-dark-mid{
    background-color: rgba(14, 14, 22, 0.719);
}

/*service section*/

.sCard{
    background: rgb(101, 120, 121);
    border-radius: 50px;
    padding: 30px;
    margin: 10px 40px 10px 40px;
    transition: 1s;
}


.sCard:hover{
    box-shadow: 5px 5px 5px 5px rgba(255, 255, 255, 0.507);
    background: rgba(77, 77, 77, 0.733);
    
}

/*projects section*/

.img-demo{
    height: 200px;    
    max-width: 200px;
    border-radius: 20%;
    filter: grayscale(.7);
    transition: .2s ease;
}

.img-demo:hover{
    border-radius: 50%;
    filter: grayscale(0);
    cursor: pointer;
}

.txt{
    font-weight: 600;
    font-variant-position: super;
    text-decoration: none;
    color: rgb(255, 255, 255);
    text-shadow: 3px 3px 3px black;
    
}

.txt:hover{
    color: tomato;
    text-decoration: none;
}

#notYet{
    cursor:not-allowed;
}

#description{
    font-weight: 500;
    text-shadow: 3px 3px 3px black;
}

li{
    list-style: none;
}