#loader{
  text-align: center;
  margin: 300px;
}


.loader {
  position: absolute;
  margin-top: 130px;
  width: 350px;
  height: 350px;
  border-radius: 50%;
  background: linear-gradient(rgb(121, 251, 255),rgb(255, 96, 228));
  -webkit-animation: animate 1s linear infinite;
          animation: animate 1s linear infinite;
}

@-webkit-keyframes animate {
  0% {
      transform: rotate(0deg);
  }
  100% {
      transform: rotate(360deg);
  }
}

@keyframes animate {
  0% {
      transform: rotate(0deg);
  }
  100% {
      transform: rotate(360deg);
  }
}

.loader span {
  position: absolute;
  width: 150px;
  height: 150px;
  border-radius: 50%;
  background: linear-gradient(rgb(121, 251, 255),rgb(255, 96, 228));
}

.loader span:nth-child(1) {
  -webkit-filter: blur(5px);
          filter: blur(5px);
}

.loader span:nth-child(2) {
  -webkit-filter: blur(10px);
          filter: blur(10px);
}

.loader span:nth-child(3) {
  -webkit-filter: blur(25px);
          filter: blur(25px);
}

.loader span:nth-child(4) {
  -webkit-filter: blur(50px);
          filter: blur(50px);
}

.loader:after {
  content: '';
  position: absolute;
  top: 10px;
  left: 10px;
  right: 10px;
  bottom: 10px;
  background: #f1f1f1;
  border: solid white 10px;
  border-radius: 50%;
}

/*Social icons*/

.slider{
  position: fixed;
  margin-top: 60px;
  height: 70vh;
  float: left;
  flex-direction: column;
  justify-content: center;
  transform: translate(-250px, 0);
}

.slider a{
  color:white;
  background: linear-gradient(45deg, rgba(121, 251, 255, 0.788),rgba(255, 96, 228, 0.788));
  font-size: 25px;
  font-weight: 500;
  text-decoration: none;
  display: block;
  margin: 5px;
  padding: 20px;
  width: 300px;
  text-align: right;
  border-radius: 50px;
  transition: .7s;
}

div .slider a:hover{
  transform: translateX(140px);
}

.social i{
  margin-left: 10px;
  font-size: 30px;
  width: 30px;
  height: 30px;
}

.fa-facebook{
  transition: 0.3s ease-in-out;
}
.fa-youtube{
  transition: 0.3s ease-in-out;
}

.fa-github{
  transition: 0.3s ease-in-out;
}

.fa-linkedin{
